import { dateRangePickerTagName } from "../constants";
import { DxDateEditBase } from "./dx-date-edit-base";
import adaptiveDropdownComponents from "../../popup/adaptivedropdowncomponents";
import calendarComponent from "../calendar/dx-calendar";
import EventHelper from "../../utils/eventhelper";
export class DxDateRangePicker extends DxDateEditBase {
    processPointerDown(e) {
        if (EventHelper.containsInComposedPath(e, this.isFieldElement.bind(this)) && !this.isDropDownOpen)
            this.tryOpenDropDown();
        return super.processPointerDown(e);
    }
}
customElements.define(dateRangePickerTagName, DxDateRangePicker);
function loadModule() { }
export default { loadModule, adaptiveDropdownComponents, calendarComponent };
